async function publishMoment() {
    const content = newMomentContent.value.trim()
    if (!content) {
        showToastMessage('', 'warning')
        return
    }
    publishingMoment.value = true
    try {
        // API
        const resp = await fetch('http://62.234.192.227/v1/api/moment/createMoment', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify({
                content: content
            })
        })
        //const data = await resp.json()
        // if (data.code === 200) {
        //     alert('')
        // } else {
        //     throw new Error(data.msg || '')
        // }
        // 
        const newMoment = {
            id: Date.now(),
            author: myName,
            content: content,
            timestamp: new Date().toISOString()
        }
        moments.value.unshift(newMoment)
        // localStorage
        const savedMoments = JSON.parse(localStorage.getItem(`moments_${sessionKey}`) || '[]')
        savedMoments.unshift(newMoment)
        localStorage.setItem(`moments_${sessionKey}`, JSON.stringify(savedMoments))
        cancelAddMoment()
    } catch (e) {
        showToastMessage(': ' + e.message, 'error')
    } finally {
        publishingMoment.value = false
    }
}

