    showAddMoment.value = false
    newMomentContent.value = ''
}

// 
async function getMomentList() {
    try {
        // API
        const resp = await fetch('http://62.234.192.227/v1/api/moment/list', {
            method: 'GET',
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
        const data = await resp.json()
        if (data.code === 0) {
            moments.value = data.data.map(item => ({
                moment_id: item.moment_id,
                user_id: item.user_id,
                author: item.username,
                content: item.content,
                likes: item.like_count,
                comments: item.comment_list,
                timestamp: item.create_time,
            }))
        }
    } catch (e) {
        showToastMessage(': ' + e.message, 'error')
    }
}

// 
function formatTime(timestamp) {
    const date = new Date(timestamp)
    const now = new Date()
    const diff = now - date
    if (diff < 60000) { // 1
        return ''
    } else if (diff < 3600000) { // 1
        return Math.floor(diff / 60000) + ''
    } else if (diff < 86400000) { // 24
        return Math.floor(diff / 3600000) + ''
    } else if (diff < 604800000) { // 7
        return Math.floor(diff / 86400000) + ''
    } else {
        return date.toLocaleDateString()
    }
}

