// 
async function submitComment(moment) {
    const content = moment.newComment?.trim()
    if (!content) {
        showToastMessage('', 'warning')
        return
    }
    submittingComment.value = true
    try {
        const momentId = parseInt(moment['moment_id'] || moment.id)
        
        // API
        const resp = await fetch('http://62.234.192.227/v1/api/comment/create', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            },
            body: JSON.stringify({
                moment_id: momentId,
                content: content
            })
        })
        
        const data = await resp.json()
        if (data.code === 0) {
            // 
            const newComment = {
                id: Date.now(),
                author: myName,
                content: content,
                timestamp: new Date().toISOString()
            }
            // 
            if (!moment.comments) {
                moment.comments = []
            }
            moment.comments.unshift(newComment)
            // 
            moment.newComment = ''
        } else {
            throw new Error(data.msg || '')
        }
    } catch (e) {
        showToastMessage(': ' + e.message, 'error')
    } finally {
        submittingComment.value = false
    }
}

// 
async function getCommentList(moment) {
    try {
        const momentId = parseInt(moment['moment_id'] || moment.id)
        const resp = await fetch(`http://62.234.192.227/v1/api/comment/list?moment_id=${momentId}`, {
            method: 'GET',
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
        const data = await resp.json()
        if (data.code === 0) {
            moment.comments = data.data.map(item => ({
                id: item.comment_id,
                author: item.username,
                content: item.content,
                timestamp: item.create_time
            }))
        }
    } catch (e) {
        showToastMessage(': ' + e.message, 'error')
        // 
        moment.comments = []
    }
}
