<template>
  <div v-if="showCallInterface" class="voice-call-overlay">
    <!--  -->
    <div v-if="callState === 'incoming'" class="call-interface incoming-call">
      <div class="caller-info">
        <div class="caller-avatar"></div>
        <h3>{{ callerName }}</h3>
        <p></p>
      </div>
      <div class="call-actions">
        <button @click="acceptCall" class="accept-btn"></button>
        <button @click="rejectCall" class="reject-btn"></button>
      </div>
    </div>

    <!--  -->
    <div v-if="callState === 'calling'" class="call-interface outgoing-call">
      <div class="caller-info">
        <div class="caller-avatar"></div>
        <h3>{{ targetName }}</h3>
        <p>...</p>
      </div>
      <div class="call-actions">
        <button @click="hangUp" class="reject-btn"></button>
      </div>
    </div>

    <!--  -->
    <div v-if="callState === 'connected'" class="call-interface active-call">
      <div class="caller-info">
        <div class="caller-avatar"></div>
        <h3>{{ targetName || callerName }}</h3>
        <p> {{ formatCallDuration(callDuration) }}</p>
      </div>
      <div class="call-controls">
        <button @click="toggleMute" :class="{ active: isMuted }" class="control-btn">
          {{ isMuted ? '' : '' }}
        </button>
        <button @click="toggleSpeaker" :class="{ active: isSpeakerOn }" class="control-btn">
          {{ isSpeakerOn ? '' : '' }}
        </button>
        <button @click="hangUp" class="reject-btn"></button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onBeforeUnmount } from 'vue'
import { getWebSocket } from './websocket'

// Props
const props = defineProps({
  myUuid: String,
  myName: String,
  messageType: Object
})

// Emits
const emit = defineEmits(['call-ended', 'call-started'])

// 
const showCallInterface = ref(false)
const callState = ref('idle') // idle, calling, incoming, connected
const callerName = ref('')
const targetName = ref('')
const targetUuid = ref('')
const isMuted = ref(false)
const isSpeakerOn = ref(false)
const callDuration = ref(0)
const callTimer = ref(null)

// WebRTC
const localStream = ref(null)
const remoteStream = ref(null)
const peerConnection = ref(null)
const iceCandidates = ref([])

// WebRTC
const rtcConfiguration = {
  iceServers: [
    { urls: 'stun:stun.l.google.com:19302' },
    { urls: 'stun:stun1.l.google.com:19302' }
  ]
}

// 
const formatCallDuration = computed(() => {
  return (duration) => {
    const minutes = Math.floor(duration / 60)
    const seconds = duration % 60
    return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
  }
})

// WebRTC
async function initWebRTC() {
  try {
    // 
    localStream.value = await navigator.mediaDevices.getUserMedia({ 
      audio: true, 
      video: false 
    })
    
    // RTCPeerConnection
    peerConnection.value = new RTCPeerConnection(rtcConfiguration)
    
    // 
    localStream.value.getTracks().forEach(track => {
      peerConnection.value.addTrack(track, localStream.value)
    })
    
    // 
    peerConnection.value.ontrack = (event) => {
      remoteStream.value = event.streams[0]
      playRemoteAudio()
    }
    
    // ICE
    peerConnection.value.onicecandidate = (event) => {
      if (event.candidate) {
        sendSignalingMessage({
          type: 'ice-candidate',
          candidate: event.candidate
        })
      }
    }
    
    // 
    peerConnection.value.onconnectionstatechange = () => {
      console.log('Connection state:', peerConnection.value.connectionState)
      if (peerConnection.value.connectionState === 'connected') {
        callState.value = 'connected'
        startCallTimer()
      } else if (peerConnection.value.connectionState === 'disconnected' || 
                 peerConnection.value.connectionState === 'failed') {
        endCall()
      }
    }
    
  } catch (error) {
    console.error('WebRTC:', error)
    alert('')
  }
}

// 
async function startCall(uuid, name) {
  targetUuid.value = uuid
  targetName.value = name
  callState.value = 'calling'
  showCallInterface.value = true
  
  await initWebRTC()
  
  // 
  sendSignalingMessage({
    type: 'call-invite',
    from: props.myUuid,
    fromName: props.myName,
    to: uuid
  })
  
  emit('call-started')
}

// 
async function acceptCall() {
  await initWebRTC()
  
  // 
  sendSignalingMessage({
    type: 'call-accept',
    from: props.myUuid,
    to: targetUuid.value
  })
  
  callState.value = 'connected'
}

// 
function rejectCall() {
  sendSignalingMessage({
    type: 'call-reject',
    from: props.myUuid,
    to: targetUuid.value
  })
  
  endCall()
}

// 
function hangUp() {
  sendSignalingMessage({
    type: 'call-hangup',
    from: props.myUuid,
    to: targetUuid.value
  })
  
  endCall()
}

// 
function endCall() {
  // 
  if (callTimer.value) {
    clearInterval(callTimer.value)
    callTimer.value = null
  }
  
  // 
  if (localStream.value) {
    localStream.value.getTracks().forEach(track => track.stop())
    localStream.value = null
  }
  
  // RTCPeerConnection
  if (peerConnection.value) {
    peerConnection.value.close()
    peerConnection.value = null
  }
  
  // 
  callState.value = 'idle'
  showCallInterface.value = false
  callDuration.value = 0
  callerName.value = ''
  targetName.value = ''
  targetUuid.value = ''
  isMuted.value = false
  isSpeakerOn.value = false
  
  emit('call-ended')
}

// 
function toggleMute() {
  if (localStream.value) {
    const audioTrack = localStream.value.getAudioTracks()[0]
    if (audioTrack) {
      audioTrack.enabled = !audioTrack.enabled
      isMuted.value = !audioTrack.enabled
    }
  }
}

// 
function toggleSpeaker() {
  isSpeakerOn.value = !isSpeakerOn.value
  // 
}

// 
function playRemoteAudio() {
  if (remoteStream.value) {
    const audioElement = new Audio()
    audioElement.srcObject = remoteStream.value
    audioElement.play().catch(console.error)
  }
}

// 
function startCallTimer() {
  callTimer.value = setInterval(() => {
    callDuration.value++
  }, 1000)
}

// 
function sendSignalingMessage(message) {
  const ws = getWebSocket()
  console.log('ws:', ws)
  console.log('props.messageType:', props.messageType)
  if (ws && ws.readyState === WebSocket.OPEN && props.messageType) {
    console.log('')
    const msgObj = {
      avatar: '',
      fromUsername: props.myName,
      from: props.myUuid,
      to: message.to || targetUuid.value,
      content: JSON.stringify(message),
      contentType: 6, // WebRTC
      type: 'webrtc-signal',
      messageType: 1,
      url: '',
      fileSuffix: '',
      file: new Uint8Array()
    }
    
    const errMsg = props.messageType.verify(msgObj)
    if (errMsg) {
      console.error(':', errMsg)
      return
    }
    console.log(':', msgObj)

    
    const messageBuffer = props.messageType.encode(props.messageType.create(msgObj)).finish()
    ws.send(messageBuffer)
  }
}

// 
function handleSignalingMessage(message) {
  const signalData = JSON.parse(message.content)
  console.log(":", signalData)

  switch (signalData.type) {
    case 'call-invite':
      // 
      callerName.value = signalData.fromName
      targetUuid.value = signalData.from
      callState.value = 'incoming'
      showCallInterface.value = true
      break
      
    case 'call-accept':
      // 
      createOffer()
      break
      
    case 'call-reject':
      // 
      alert('')
      endCall()
      break
      
    case 'call-hangup':
      // 
      endCall()
      break
      
    case 'offer':
      handleOffer(signalData.offer)
      break
      
    case 'answer':
      handleAnswer(signalData.answer)
      break
      
    case 'ice-candidate':
      handleIceCandidate(signalData.candidate)
      break
  }
}

// Offer
async function createOffer() {
  try {
    const offer = await peerConnection.value.createOffer()
    await peerConnection.value.setLocalDescription(offer)
    
    sendSignalingMessage({
      type: 'offer',
      offer: offer
    })
  } catch (error) {
    console.error('Offer:', error)
  }
}

// Offer
async function handleOffer(offer) {
  try {
    await peerConnection.value.setRemoteDescription(offer)
    
    const answer = await peerConnection.value.createAnswer()
    await peerConnection.value.setLocalDescription(answer)
    
    sendSignalingMessage({
      type: 'answer',
      answer: answer
    })
  } catch (error) {
    console.error('Offer:', error)
  }
}

// Answer
async function handleAnswer(answer) {
  try {
    await peerConnection.value.setRemoteDescription(answer)
  } catch (error) {
    console.error('Answer:', error)
  }
}

// ICE
async function handleIceCandidate(candidate) {
  try {
    await peerConnection.value.addIceCandidate(candidate)
  } catch (error) {
    console.error('ICE:', error)
  }
}

// 
defineExpose({
  startCall,
  handleSignalingMessage,
  endCall
})

// 
onBeforeUnmount(() => {
  endCall()
})
</script>

<style scoped>
.voice-call-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.call-interface {
  background: white;
  border-radius: 20px;
  padding: 40px;
  text-align: center;
  min-width: 300px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.caller-info {
  margin-bottom: 30px;
}

.caller-avatar {
  font-size: 60px;
  margin-bottom: 15px;
}

.caller-info h3 {
  margin: 0 0 10px 0;
  font-size: 24px;
  color: #333;
}

.caller-info p {
  margin: 0;
  color: #666;
  font-size: 16px;
}

.call-actions {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.call-controls {
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}

.accept-btn {
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 15px 30px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s;
}

.accept-btn:hover {
  background: #45a049;
}

.reject-btn {
  background: #f44336;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 15px 30px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s;
}

.reject-btn:hover {
  background: #da190b;
}

.control-btn {
  background: #f0f0f0;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 20px;
  cursor: pointer;
  transition: background 0.3s;
}

.control-btn:hover {
  background: #e0e0e0;
}

.control-btn.active {
  background: #2196F3;
  color: white;
}

.incoming-call {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
</style>